@use 'base/variables' as v;
@use 'base/mixins' as m;
.subtitulo{
    padding: 1rem 0;
    h3{
        color: v.$blanco;
    }
    &.uno{ 
        background-color: #FCB424;
    }
    &.dos{
        background-color: #EA4B8B;
    }
    &.tres{
        background-color: #63C5EA;
    }
    &.cuatro{
        background-color: #7F59B0;
    }
    &.cinco{
        background-color: #9b8ab0;
    }
}
.integrantes{
    &__grid{
        @include m.tablet {
            @include m.grid(2,3rem);

        }
    }
    p{
        margin: 0;
        padding: 1rem;
        color: #27798F;
        font-size: 2rem;
    }
    .miembros{
        text-align: justify;
        background-color: v.$blanco;
    }
}
.ultimo{
    margin-top: 2rem;
}
.profes{
    &__grid{
        background-color: #FFF4E6;
        @include m.tablet {
            @include m.grid(3,1rem);
        }
    }
    &__mask{
        display: grid;
        place-items: center;
    }
}
.profesores{
    @include m.tablet {
        grid-column: 1/3;
    }
}
.profesUno{
    // background-image: url('../img/');
    height: 10rem;
    object-fit: contain;
    width: 8rem;
}