body {
  font-family: "Acme", sans-serif;
  font-size: 1.6rem;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
body.fijarbody {
  overflow: hidden;
}

main {
  flex: 1;
}

.contenedor {
  width: 90%;
