  margin: 2rem 0;
}

.w-50 {
  max-width: 70%;
  margin: 0 auto;
}

.contacto__titulo {
  text-align: center;
  color: #001851;
  margin: 2rem 0;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.5rem;
}
