  .contacto__titulo {
    font-size: 3rem;
  }
}
.contacto__subtitulo {
  text-align: center;
  color: #001851;
  font-weight: 300;
  font-size: 2.5rem;
  margin: 2rem 0;
}

form legend {
  text-transform: uppercase;
  font-weight: bold;
  color: black;
}
form fieldset {
  border: none;
  margin-bottom: 1.5rem;
}
form fieldset:last-of-type {
  margin-bottom: 0;
}
form p {
  font-size: 1.4rem;
  margin: 0;
  color: black;
}
form textarea {
  display: block;
  width: 100%;
