  border: 1px solid black;
}

.campo {
  margin-bottom: 1.5rem;
}
.campo:last-of-type {
  margin-bottom: 0;
}
.campo__label {
  text-transform: uppercase;
  color: #13254f;
  font-weight: bold;
}
.campo__input {
  display: block;
  width: 100%;
  padding: 1rem;
  background-color: #FFFFFF;
  border: 0.1rem solid #606060;
  border-radius: 1rem;
}
.campo__input::placeholder {
  color: #606060;
}
.campo__radios {
  display: flex;
  column-gap: 1.5rem;
  margin-bottom: 1rem;
}
.campo__radio {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

