  text-align: center;
  background-color: #5185FF;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border-radius: 2rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  margin: 2rem 0;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .btn-enviar {
    width: auto;
  }
}
.btn-enviar:hover {
  background-color: #1e61ff;
}
@media (min-width: 768px) {
  .btn-enviar {
    display: inline-block;
  }
}

