.mg-3 {
  margin: 2rem 0;
}

.lista {
  width: 100%;
  margin: 4rem auto;
  border-spacing: 0;
}
.lista thead {
  background-color: #EB566E;
}
.lista th {
  color: #FFFFFF;
  padding: 1rem 0;
}
.lista img {
  margin: 0 auto;
  width: 15rem;
}
.lista a {
  margin: 0;
  margin-bottom: 1rem;
  padding: 1rem;
  display: block;
}
.lista td {
  text-align: center;
  color: #1a1b15;
}

.form_ajustar {
  display: grid;
  place-items: center;
}
.form_ajustar__btn {
  background-color: #EB566E;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border-radius: 2rem;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
