}
.posponer button i {
  color: red;
}

.tabla__fondo {
  background-image: url("../img/FondoRompecabezas.png");
  background-size: cover;
  width: 100%;
}
@media (min-width: 768px) {
  .tabla__fondo {
    background-position: center center;
  }
}
@media (min-width: 1024px) {
  .tabla__fondo {
    background-position: top center;
    width: 100%;
  }
}

.tabla__rompe {
  background-image: url("../img/Rompecabezas/4girado.png"), url("../img/Rompecabezas/3girado.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}
@media (min-width: 1024px) {
  .tabla__rompe {
    background-size: 23rem;
    background-position: left bottom, right;
    width: 100%;
  }
}

.header {
  background-color: #73C5EA;
  position: sticky;
  top: 0;
}
@media (min-width: 768px) {
  .header {
    position: static;
  }
}
.header__barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  display: flex;
  align-items: center;
}
.header__img {
  width: 5rem;
  height: 5rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .header__img {
    width: 6rem;
    height: 6rem;
  }
}
.header__titulo {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2rem;
  margin-left: 1rem;
}

.nav {
  display: none;
}
.nav li {
  display: inline;
  padding: 1.3rem 0.4rem;
}
.nav__a {
  display: inline-block;
  margin-top: 1.6rem;
  font-size: 1.9rem;
  color: #FFFFFF;
}
.nav__a i {
  margin-right: 0.2rem;
}
@media (min-width: 768px) {
  .nav__a {
    margin-right: 1rem;
  }
}
@media (min-width: 1024px) {
  .nav__a {
    margin-right: 1.3rem;
  }
}
@media (min-width: 768px) {
  .nav {
    display: block;
  }
}
.nav__img {
  width: 5rem;
  height: 5rem;
  object-fit: contain;
}

.none {
  display: none;
}
@media (min-width: 768px) {
  .none {
    display: inline;
  }
}

.menu i {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.nav > li {
  float: left;
}

.nav li ul {
  display: none;
  position: absolute;
  background-color: #5185FF;
}

.nav li:hover > ul {
  display: block;
  margin-top: 1rem;
}

.nav li ul li {
  position: relative;
}

.nav li ul li a {
  display: block;
  color: #FFFFFF;
}
.nav li ul li a:hover {
  background-color: #1e61ff;
}

.contenedor-menu {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  top: 7.5rem;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .contenedor-menu {
    display: none;
  }
}
.contenedor-menu a {
  text-align: center;
  display: block;
  color: #FFFFFF;
  padding: 2rem;
  background-color: #73C5EA;
  margin: 0.5rem 0;
  transition: background-color, color 0.4s ease-in-out;
}
.contenedor-menu a:hover {
  cursor: pointer;
  background-color: #b6e1f4;
  font-weight: bold;
}
.contenedor-menu a i {
  margin-right: 0.8rem;
}

.definicion {
  margin: 2rem auto;
